.progress-bar {
  position: absolute;
  bottom: 0;
  left: 35px;
  right: 0;
  height: 1px;
  background: $grey-border;
}

.progress-bar__count {
  position: absolute;
  right: calc(100% + 5px); 
  font-size: .81rem;
  line-height: 1;
  top: 50%;
  transform: translateY(-50%);
  white-space: nowrap;
}

.progress-bar__count-second {
  color: #828282;
}

.progress-bar__line {
  position: absolute;
  left: 0;
  top: 0;
  height: 1px;
  background: $white;
  transition: transform .35s ease;
  width: 100%;
  transform: scaleX(0);
  transform-origin: left;
}