.news {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 70px auto 85px;
  width: 95%;
}

.news__item {
  flex: 1 0 50%;
  border: 1px solid $grey-border;
  transition: 0.45s;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
  }
}

.news-data {
  font-size: 14px;
  padding-top: 25px;
  padding-left: 59px;
  margin-bottom: 25px;
}

.news-title {
  font-size: 16px;
  display: block;
  font-weight: 600;
  color: $white;
  width: 100%;
  max-width: 464px;
  position: relative;
  padding-bottom: 37px;
  padding-left: 59px;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: -95px;
    background: url("../img/sprites/sprite.svg#news-bullet") no-repeat center;
    background-size: cover;
    width: 60px;
    height: 40px;
  }
}