.languages {
  position: relative;
}

.languages__link {
  display: block;
  border: none;
  background: none;
  color: $grey-opaque;
  font-size: 16px;
  font-weight: 300;
  outline: none;
  cursor: pointer;
  margin-bottom: 23px;

  &.is-active {
    opacity: 1;
    pointer-events: auto;
    color: $white;
    
    &:hover {
      opacity: 1;
    }
  }
}

.languages__wrapper {
  position: absolute;
  bottom: 100%;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.35s;

  .languages:hover &,
  body.is-static & {
    opacity: 1;
    pointer-events: auto;
  }
}

button.languages__link {
  padding-left: 0;
  padding-right: 0;
}

