.burger {
  position: relative;
  background: transparent;
  width: 28px;
  height: 20px;
  border: 0;
  outline: none;
  cursor: pointer;
  padding: 0; 

  body.is-static & {
    span:first-child {
      top: 9px;
      transform: rotate(45deg);
    }

    span:nth-child(2) {
      left: -30px;
      opacity: 0;
    }

    span:last-child {
      transform: rotate(-45deg);
      bottom: 9px;
    }
  }

  span {
    position: absolute;
    width: 100%;
    height: 2px;
    background: #BDBDBD;
    left: 0;
    transition: all .35s ease;
    
    &:first-child {
      top: 0;
    }

    &:nth-child(2) {
      top: 50%;
      transform: translateY(-50%);
    }

    &:last-child {
      bottom: 0;
    }
  }
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-top: 18vh;
  background: linear-gradient(-90deg, $background-color 50%, rgba($background-color, 0) 50%);
  background-size: 200%;
  background-position: left;
  transition: background-position .45s ease .45s;
  pointer-events: none;
  z-index: 3;

  &.is-active {
    background-position: right;
    transition: background-position .45s ease;
    pointer-events: auto;
  }
}

.menu__col {
  flex: 1 0 50%;
  display: flex;
  flex-direction: column;
  opacity: 0;
  transition: opacity .45s ease;
  width: 50%;
  align-items: flex-start;

  .menu.is-active & {
    opacity: 1;
    transition: opacity .45s ease .45s;
  }
}

.copyright--menu {
  opacity: 0;
  margin-top: 118px;

  .menu.is-active & {
    opacity: 1;
    transition: opacity .45s ease .45s;
  }
}

.wrapper-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 35px;
}

.menu__link {
  font-size: 36px;
  line-height: 1.5;
  color: $white;
  margin-bottom: 25px;
  position: relative;

  &:hover {
    &::after {
      transform: scaleX(1);
      transform-origin: left;
    }
  }

  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $white;

    transform: scaleX(0);
    transform-origin: right;
    transition: transform 250ms ease-in;
  }


  &:last-of-type {
    margin-bottom: 0;
  }
}