.lines {
  position: fixed;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
}

.lines__line {
  position: absolute;
  top: 0;
  width: 1px;
  height: 100vh;
  background: $grey-border;
}

.lines__line--left {
  left: 0;
}

.lines__line--center {
  left: 50%;
  transform: translateX(-50%);
}

.lines__line--right {
  right: 0;
}

