*,
*::after,
*::before {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

html {
  font-size: 16px;
}

body {
  max-width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  background: $background-color;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.3;
  color: $white;

  &.is-static {
    overflow-y: hidden;
    height: 100vh;
  }
}

.container {
  width: 100%;
  max-width: 1217px;
  padding: 0;
  margin: 0 auto;
}

