.wrapper-tabs {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  margin-bottom: 30px;
}

.tabs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.tab {
  padding-top: 44px;
  padding-left: 44px;
  position: relative;
  height: calc(100% / 3);
  border-bottom: 1px solid $grey-border;

  &:first-of-type {
    border-top: 1px solid $grey-border;
  }
}

.js-tab-content {
  display: none;

  &.is-active {
    display: block;
    height: 100%;
  }
}

.team-member-name {
  font-size: 18px;
  font-weight: 300;
  display: block;
}

.tab.is-active {
  .team-member-name {
    font-weight: 600;
  }
}

.team-member-position {
  font-size: 16px;
  color: $grey;
}

.wrapper-tabs__item {
  width: 50%;
}

.team-member-photo {
  height: 100%;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper-team__item--tabs {
  margin-top: 55px;
}

.js-tab-content.is-active {
  height: 100%;
}


// Tabs about

.tabs-about {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex: 1 0 40%;
}

.tabs-about-content {
  flex: 1 0 60%;
}

.tab-about {
  position: relative;
  height: 130px;
  cursor: pointer;
  border-right: 1px solid $grey-border;
  border-bottom: 1px solid $grey-border;

  &:last-of-type {
    border-bottom: none;
  }
}

.tab-about__title {
  font-size: 14px;
  font-weight: 300;
  text-transform: capitalize;
  padding-top: 50px;
  padding-left: 125px;
  width: 100%;
}

.tab-about.is-active {
  border-right: none;

  .tabs-about__logo {
    fill: $whitish;
  }
}

.tab-about-content {
  padding-right: 20px;
}

.tabs-about__logo {
  fill: $grey-opaque;
}

.tabs-about__svg {
  position: absolute;
  top: 50%;
  left: 40px;
  transform: translateY(-50%);
}

//tabs inner

.tab-inner {
  border-bottom: 1px solid $grey-border;

  &:last-of-type {
    border-bottom: none;
  }
}

.tab-inner__title {
  padding: 35px 0 35px 95px;
  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    background: url("../img/sprites/sprite.svg#tab-inner-bullet") no-repeat center;
    background-size: cover;
    width: 27px;
    height: 19px;
    top: 50%;
    left: 33px;
    transform: translateY(-50%);
    opacity: 0;
  }
}

.tabs-inner, .tabs-inner-content {
  flex: 1 0 50%;
}

.tabs-inner {
  border-right: 1px solid $grey-border;
}

.tab-inner__content {
  padding: 35px 0 35px 35px;
  display: none;

  &.is-active {
    display: block;
    height: 100%;
  }
}

.tab-inner.is-active {
  .tab-inner__title::before {
    opacity: 1;
  }
}