$size: 706px;         // project size
$max-w: 10em;         // biggest wave
$min-w: .25em;        // smallest wave
$r: $max-w - $min-w;  // width range
$speed: 2500ms;       // wave speed
$f: 20;               // number of waves
$s-w: 0.1em;          // wave width
$c: #3E3566;          // wave color

.wave-container {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: $max-w;
  height: $max-w / 2;
  font-size: ($size / ($max-w / 1em));
  overflow: hidden;
  z-index: -2;
}

.wave {
  border-width: $s-w;
  border-style: solid;
  position: absolute;
  animation: wave $speed infinite alternate;
  transform: rotate(-44.5deg);
  $a: 1; // alpha
  @for $i from 1 through $f {
    &:nth-child(#{$i}) {
      $w: $max-w - (($r/$f) * $i);
      @if $i > 1 { $a: 1 - ((1 / ($f+1)) * $i); }
      width: $w;
      height: $w;
      bottom: $w/-2;
      left: ($max-w - $w ) / 2;
      animation-delay: $i * ($speed / $f);
      border-color: transparent transparent rgba($c,$a) rgba($c,$a);
      border-radius: $w;
    }
  }
}

@keyframes wave {
  to { transform: rotate(135deg); }
}