.hero__wrapper {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 46%;
  z-index: 2;
}

.hero-text-container {
  display: flex;
  justify-content: space-between;
  transform: translateX(-50%);
}

.hero-text {
  font-size: 24px;
  font-weight: 300;
  color: $whitish;
  letter-spacing: 0.1em;
  position: absolute;
  left: 50%;
  white-space: nowrap;
}

.hero-text-1 {
  top: 16px;
}

.hero-text-1--right {
  margin-left: 54px;
}

.hero-text-2 {
  top: 85px;
  left: 56px;
  animation: slide-from-right 1.5s ease-out 0.1s;
  animation-fill-mode: both;
}

.hero-text-3 {
  top: 154px;
  left: 54px;
}

.hero-text-3--right {
  margin-left: 54px;
}

.hero-text-4 {
  top: 222px;
  left: 46px;
  animation: slide-from-right 1.5s ease-out 0.3s;
  animation-fill-mode: both;
}

.hero-text-5 {
  top: 291px;
  left: 59px;
}

.hero-text-5--right {
  margin-left: 43px;
}

.hero-text-6 {
  top: 361px;
  left: 35px;
}

.hero-text-6--right {
  margin-left: 43px;
}

.hero-text-7 {
  top: 430px;
  left: 13px;
}

.hero-text-7--right {
  margin-left: 43px;
}

.hero-text-1--left {
  animation: slide-from-left 1.5s ease-out;
  animation-fill-mode: both;
}

.hero-text-3--left {
  animation: slide-from-left 1.5s ease-out 0.2s;
  animation-fill-mode: both;
}

.hero-text-5--left {
  animation: slide-from-left 1.5s ease-out 0.4s;
  animation-fill-mode: both;
}

.hero-text-6--left {
  animation: slide-from-left 1.5s ease-out 0.5s;
  animation-fill-mode: both;
}

.hero-text-7--left {
  animation: slide-from-left 1.5s ease-out 0.6s;
  animation-fill-mode: both;
}

.map-about-main {
  width: 572.55px;
  height: 359.51px;
  object-fit: contain;
  margin-bottom: 45px;
}

.wrapper--about {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-left: 35px;
}

.wrapper--about__map {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 10px;
}

.wrapper-partners {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.about-text {
  font-size: 18px;
  font-weight: 300;
  margin: 150px 0;
  width: 100%;
  max-width: 515px;
}

.about-locations {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.about-locations__item {
  margin-right: 40px;
  position: relative;

  &:last-of-type {
    margin-right: 20px;
  }

  &::before {
    position: absolute;
    content: '';
    background: url("../img/sprites/sprite.svg#about-bullets") no-repeat center;
    background-size: cover;
    width: 14px;
    height: 12px;
    top: 50%;
    left: -14px;
    transform: translate(-50%, -50%);
  }
}

.services {
  margin: 70px 0 120px;
  padding-left: 50px;
}

.services__item {
  font-size: 18px;
  position: relative;
  margin-bottom: 30px;

  &:last-of-type {
    margin-bottom: 0;
  }

  &::before {
    position: absolute;
    top: 40%;
    left: -25px;
    transform: translate(-50%, -50%);
    content: '';
  }
}

.services__item--1 {
  &::before {
    background: url("../img/sprites/sprite.svg#services__item--1") no-repeat center;
    background-size: cover;
    width: 23.7px;
    height: 16.5px;
  }
}

.services__item--2 {
  &::before {
    background: url("../img/sprites/sprite.svg#services__item--2") no-repeat center;
    background-size: cover;
    width: 16px;
    height: 19.5px;
  }
}

.services__item--3 {
  &::before {
    background: url("../img/sprites/sprite.svg#services__item--3") no-repeat center;
    background-size: cover;
    width: 20.3px;
    height: 20px;
  }
}

.services__item--4 {
  &::before {
    background: url("../img/sprites/sprite.svg#services__item--4") no-repeat center;
    background-size: cover;
    width: 20.7px;
    height: 21.5px;
  }
}

.services__item--5 {
  &::before {
    background: url("../img/sprites/sprite.svg#services__item--5") no-repeat center;
    background-size: cover;
    width: 19.5px;
    height: 18px;
  }
}

.services__item--6 {
  &::before {
    background: url("../img/sprites/sprite.svg#services__item--6") no-repeat center;
    background-size: cover;
    width: 22.4px;
    height: 18.4px;
  }
}

.services__item--7 {
  &::before {
    background: url("../img/sprites/sprite.svg#services__item--7") no-repeat center;
    background-size: cover;
    width: 21.6px;
    height: 16.7px;
  }
}

.wrapper-services {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.wrapper-services__item {
  flex: 1 0 50%;
}

.wrapper-services__item--img {
  text-align: center;
  position: relative;
}

.services-img {
  width: 337px;
  height: 334.5px;
  object-fit: cover;
  position: relative;
}

.services-img-animation {
  position: absolute;
  width: 425px;
  height: 425px;
  top: -47px;
  object-fit: cover;
  left: 90px;
  animation: services 15s linear infinite;
}

.partners-logo--2 {
  width: 132px;
  height: 26px;
  object-fit: cover;
}

.partners-logo {
  display: block;
  margin-bottom: 78px;

  &:first-of-type {
    margin-top: 140px;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.wrapper-partners__item {
  width: 50%;
}

