.team-text {
  font-size: 18px;
  font-weight: 300;
  margin-top: 100px;
  width: 100%;
  max-width: 480px;
}

.wrapper-team {
  display: flex;
  justify-content: space-between;
}

.wrapper-team__item {
  width: 50%;
}

.wrapper-contacts {
  flex: 1 0 50%;
  margin-bottom: 35px;

  &:last-child {
    margin-bottom: 0;
  }
}

.contacts {
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
  flex: 1 0 50%;
  padding-left: 35px;
}

.contacts__item {
  position: relative;
  font-size: 16px;
  font-weight: 600;
  display: block;
  color: $white;
  margin-bottom: 35px;
  padding-left: 35px;

  &::before {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translate(-50%, -50%);
    content: '';
  }
}

.contacts__item--pin {
  &::before {
  background: url("../img/sprites/sprite.svg#contacts__item--pin")no-repeat center;
  background-size: cover;
  width: 14px;
  height: 20px;
  }
}

.contacts__item--phone {
  &::before {
    background: url("../img/sprites/sprite.svg#contacts__item--phone")no-repeat center;
    background-size: cover;
    width: 15px;
    height: 15px;
  }
}

.contacts__item--mail {
  &::before {
    background: url("../img/sprites/sprite.svg#contacts__item--mail")no-repeat center;
    background-size: cover;
    width: 18px;
    height: 14.4px;
  }
}

.contacts__item--social {
  &::after {
    background: url("../img/sprites/sprite.svg#contacts__item--social")no-repeat center;
    background-size: cover;
    left: 280px;
    width: 105px;
    height: 18px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    content: '';
  }
}

.copyright {
  font-size: 14px;
  mix-blend-mode: normal;
  padding: 17px 0 17px 35px;
  border-top: 1px solid $grey-border;
}

.copyright--borderless {
  border-color: transparent;
}

.google-map {
  position: relative;
  z-index: 2;
  display: flex;
}

.padding-left {
  padding-left: 35px;
}

