.dropdown {
  position: relative;
  margin-right: 15px;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    right: -1px;
    border: $white solid;
    border-width: 0 1px 1px 0;
    transform: rotate(45deg);
    width: 8px;
    height: 8px;
  }
}

.dropdown__item {
  font-size: 14px;
  font-weight: 300;
  margin-right: 20px;
  color: $white;
  display: block;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  margin-bottom: 20px;

    &:hover {
      opacity: 1;
      pointer-events: auto;
    }
}

.dropdown__list {
  position: absolute;
  top: 100%;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.35s;
  z-index: 2;

  .dropdown:hover & {
    opacity: 1;
    pointer-events: auto;
  }
}


