.link {
  cursor: pointer;
  outline: 0;
  color: $white;
}

.link--arrow {
  position: relative;
  font-weight: 300;

  &:hover {
    &::after {
      transition: 0.5s ease;
      transform: translate(10px,-50%);
    }
  }

  &::after {
    position: absolute;
    top: 50%;
    right: -50px;
    transform: translateY(-50%);
    content: '';
    background: url("../img/sprites/sprite.svg#link--arrow") no-repeat center;
    background-size: cover;
    width: 36px;
    height: 7px;
  }
}

.link-team {
  margin-left: 34px;
}

.js-tab-content.is-active {
  border: 0;
  display: block;
  height: 100%;
}
