.title-hero {
  font-size: 48px;
  font-weight: 800;
  color: $whitish;
  letter-spacing: 4px;
  margin: 0 auto;
  writing-mode: vertical-rl;
  text-orientation: upright;
  text-transform: uppercase;

  position: relative;
  z-index: 3;
}

.main-title {
  font-size: 48px;
}

.subtitle {
  font-size: 36px;
  padding-top: 70px;
  margin-bottom: 30px;
}

.subtitle-small {
  font-size: 24px;
  margin-bottom: 30px;
}

.text--section {
  font-size: 18px;
  font-weight: 300;
}

.text--semibold {
  font-weight: 600;
}

.text--paragraph {
  font-size: 16px;
  font-weight: 300;
  margin-bottom: 40px;
}