.team {
  margin-top: 70px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.team__item {
  position: relative;
  margin-bottom: 40px;
  cursor: pointer;

  &:hover {
    .team-member {
      transition: 0.35s ease-out;
      filter: brightness(75%);
    }

    .team-member-info {
      display: block;
    }
  }
}

.team-member-info {
  display: none;
  position: absolute;
  bottom: 50px;
  left: 25px;
}

.team-member__name {
  font-size: 18px;
  font-weight: 600;
  display: block;
}

.team-member__position {
  font-size: 16px;
}
