@keyframes services {
  0%   {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes slide-from-left {
  0%   {
    margin-right: 700px
  }

  100% {
    margin-right: 0;
  }
}

@keyframes slide-from-right {
  0%   {
    left: 400px
  }

  100% {
    left: 56px;
  }
}