.services-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.services-tabs {
  display: flex;
  justify-content: space-between;
  margin-left: 70px;
}

.service-tab__title {
  font-size: 14px;
  color: $grey-opaque;
  padding-top: 35px;
  width: 100%;
  max-width: 115px;
  text-align: center;
  position: relative;
  cursor: pointer;
}

.service-tab.is-active {
  .service-tab__title {
    color: $white;
  }

  .service-tab__logo {
    fill: $whitish;
  }
}

.service-tab {
  position: relative;
  margin-right: 30px;

  &:last-of-type {
    margin-right: 10px;
  }
}

.service-tab__svg {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  content: '';
}

.service-tab__logo {
  fill: $grey-opaque;
}

.flex-service-tab-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  border-top: 1px solid $grey-border;
}

