.service-tab__title.is-active {
  &::before {
    fill: $grey;
  }
}

.text-content, .img-content {
  flex: 1 0 50%;
}

.text-content {
  padding-right: 90px;
  border-right: 1px solid $grey-border;
}

.advantages__item {
  font-size: 16px;
  font-weight: 300;
  margin-bottom: 20px;
  position: relative;
  padding-left: 28px;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(50%);
    width: 10px;
    height: 10px;
    background-color: $white;
  }
}

.services-tab-img {
  max-width: 100%;
  padding-top: 70px;
}

.services-img-big, .services-bottom-flex-text {
  flex: 1 0 50%;
}

.services-img-big {
  width: 243px;
  height: 171.5px;
}

.services-img-big__svg {
  fill: none;
  stroke: $white;
  stroke-width: 0.1px;
}

.services-bottom-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid $grey-border;
}

.services-bottom-flex-text {
  padding: 60px 25px;
  border-left: 1px solid $grey-border;
}

