.about {
  display: flex;
  justify-content: space-between;
}

.about__item--1 {
  flex: 1 0 40%;
}

.about__item--2 {
  flex: 1 0 60%;
}

.text-about {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
  width: 100%;
  max-width: 422px;
  margin-top: 70px;
}

.tabs-about-wrapper {
  display: flex;
  justify-content: space-between;
  border: 1px solid $grey-border;
}

.tabs-about-content__title {
  padding: 60px 0 0 27px;
  font-size: 24px;
  margin-bottom: 60px;
}

.tabs-about-content__text {
  padding-left: 55px;
  font-size: 16px;
  font-weight: 300;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 27px;
    width: 10px;
    height: 10px;
    background-color: $white;
  }
}