.sidebar {
  position: fixed;
  top: 0;
  right: 0;
  width: 68px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  padding: 20px 0;
  z-index: 5;
}

.social {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.social__item {
  margin-bottom: 35px;
  outline: none;
  border: none;

  &:last-of-type {
    margin-bottom: 0;
  }

  &:hover {
    transform: scale(1.1);
  }
}

.social__item--instagram {
  background: url("../img/sprites/sprite.svg#social__item--instagram") no-repeat center;
  background-size: cover;
  width: 18.6px;
  height: 18.8px;
}

.social__item--facebook {
  background: url("../img/sprites/sprite.svg#social__item--facebook") no-repeat center;
  background-size: cover;
  width: 9.3px;
  height: 17px;
}

.social__item--twitter {
  background: url("../img/sprites/sprite.svg#social__item--twitter") no-repeat center;
  background-size: cover;
  width: 23.5px;
  height: 19.1px;
}

